import { useState } from "react";
import ProductList from "../components/ProductList";
import CartSummary from "../components/CartSummary";
import Pagination from "../components/Pagination";
import Head from "next/head";

export default function Home({ products, page, totalPages }) {
  const [cart, setCart] = useState({}); // { productId: quantity }

  const addToCart = (productId) => {
    setCart((prev) => {
      const qty = prev[productId] || 0;
      return { ...prev, [productId]: qty + 1 };
    });
  };

  const updateQuantity = (productId, quantity) => {
    setCart((prev) => {
      if (quantity <= 0) {
        const newCart = { ...prev };
        delete newCart[productId];
        return newCart;
      }
      return { ...prev, [productId]: quantity };
    });
  };

  return (
    <div className="min-h-screen bg-gray-50 px-6 py-10">
      <Head>
        <title>Trendy Store - Shop the Latest Trends</title>
        <meta name="description" content="Shop trendy products like T-shirts, shoes, hats, and more!" />
      </Head>

      <header className="flex justify-between items-center mb-10">
        <h1 className="text-3xl font-extrabold text-gray-900">🛍️ Trendy Store</h1>
        <CartSummary cart={cart} />
      </header>

      <ProductList products={products} addToCart={addToCart} updateQuantity={updateQuantity} cart={cart} />
      
      <Pagination currentPage={page} totalPages={totalPages} />
    </div>
  );
}

export async function getServerSideProps({ query }) {
  const page = parseInt(query.page) || 1;
  const limit = 20;
  const offset = (page - 1) * limit;

  // Fetch the paginated products from your API
  const res = await fetch(`http://localhost:4000/products?limit=${limit}&offset=${offset}`);
  const data = await res.json();
  
  // Simulating the total count for pagination (you could get this from your database or API)
  const totalProducts = 3000; // Assume total product count
  const totalPages = Math.ceil(totalProducts / limit);

  return {
    props: {
      products: data.products,
      page,
      totalPages,
    },
  };
}
