export default function CartSummary({ cart }) {
    const totalItems = Object.values(cart).reduce((sum, quantity) => sum + quantity, 0);
    const totalPrice = Object.entries(cart).reduce((sum, [id, quantity]) => {
      // Simulate getting the product price, ideally you'd fetch this from state or the server
      const productPrice = 20; // Example static price
      return sum + productPrice * quantity;
    }, 0);
  
    return (
      <div className="text-lg text-gray-800">
        🛒 {totalItems} items - ${totalPrice.toFixed(2)}
      </div>
    );
  }
  