export default function ProductList({ products, addToCart, updateQuantity, cart }) {
    return (
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-4">
        {products.map((product) => (
          <div key={product.id} className="border p-4 rounded bg-white shadow-md">
            <h2 className="text-xl font-semibold text-gray-800">{product.name}</h2>
            <p className="text-gray-600">${product.price}</p>
            
            <div className="flex items-center gap-2 mt-2">
              <button
                onClick={() => addToCart(product.id)}
                className="bg-blue-500 text-white px-3 py-2 rounded hover:bg-blue-600"
              >
                Add to Cart
              </button>
  
              {cart[product.id] && (
                <input
                  type="number"
                  value={cart[product.id]}
                  onChange={(e) =>
                    updateQuantity(product.id, parseInt(e.target.value) || 0)
                  }
                  className="w-16 border px-2 py-1 rounded focus:ring-2 focus:ring-indigo-500"
                />
              )}
            </div>
          </div>
        ))}
      </div>
    );
  }
  